<template lang="html">
<div id="elem">
    <div id="map"></div>
    <div class="layer-info noselect" v-html="selectedFeature" v-if="isLayer01Active || isLayer02Active || isLayer03Active || isLayer04Active || isLayer06Active || isLayer07Active"></div>
    <div class="nav-container noselect">
        <div class="nav-help scenario">Оберіть сценарій затоплення</div>

        <nav class="scenario narrow">
            <div>
                <ul>
                    <li class="overlay default" v-on:click="layerZone1Click" v-bind:class="{ selected: isZone1Active }"><a href="#">{{layerZone01.name}}</a><br>
                      <span class="subtitle">Короткий опис</span>
                    </li>
                </ul>
                <ul>
                    <li class="overlay" v-on:click="layerZone2Click" v-bind:class="{ selected: isZone2Active }"><a href="#">{{layerZone02.name}}</a><br>
                      <span class="subtitle">Короткий опис</span>
                    </li>
                </ul>
            </div>
            <transition name="fade">
              <div class="infobox" style="display: block;" v-show="isZone1Active || isZone2Active">{{zoneDescription}}</div>
            </transition>
        </nav>

        <div class="nav-help theme">Для перегляду наслідків затоплення оберіть категорію</div>

        <nav class="theme narrow">
            <div>
                <ul>
                    <li class="overlay" v-on:click="layer01Click" v-bind:class="{ selected: isLayer01Active }"><a href="#">{{layer01.name}}</a></li>
                    <li class="overlay" v-on:click="layer02Click" v-bind:class="{ selected: isLayer02Active }"><a href="#">{{layer02.name}}</a></li>
                    <li class="overlay" v-on:click="layer03Click" v-bind:class="{ selected: isLayer03Active }"><a href="#">{{layer03.name}}</a></li>

                </ul>
                <ul>
                    <li class="overlay" v-on:click="layer04Click" v-bind:class="{ selected: isLayer04Active }"><a href="#">{{layer04.name}}</a></li>
                    <!-- <li class="overlay" v-on:click="layer05Click" v-bind:class="{ selected: isLayer05Active }"><a href="#">{{layer05.name}}</a></li> -->
                    <li class="overlay" v-on:click="layer06Click" v-bind:class="{ selected: isLayer06Active }"><a href="#">{{layer06.name}}</a></li>
                    <li class="overlay" v-on:click="layer07Click" v-bind:class="{ selected: isLayer07Active }"><a href="#">{{layer07.name}}</a></li>
                </ul>
            </div>
            <transition name="fade">
              <div class="infobox" style="display: block;" v-html="layerLegendHTMLcontent" v-show="isLayer01Active || isLayer02Active || isLayer03Active || isLayer04Active || isLayer06Active || isLayer07Active">
              </div>
            </transition>
        </nav>

    </div>

</div>
</template>

<script>
import * as L from "leaflet";

L.control.iconLayers = require("../iconlayers-0.2.0/iconLayers.js");
L.GPlaceAutocomplete = require("../leaflet-google-places-autocomplete/leaflet-gplaces-autocomplete.js");

L.esri = require("esri-leaflet");

const axios = require("axios");
const mapServerUrl =
  "https://gispzf.westeurope.cloudapp.azure.com/arcgis/rest/services/Sea/Ukraine_Sea_Change_Map/MapServer";

export default {
  data() {
    return {
      map: [],
      isZone1Active: false,
      isZone2Active: false,

      selectedFeature:
        "Наблизьтесь до потрібної ділянки карти та клацніть для отримання додаткових відомостей про об’єкт",

      zoneDescription: "",

      isLayer01Active: false,
      isLayer02Active: false,
      isLayer03Active: false,
      isLayer04Active: false,
      // isLayer05Active: false,
      isLayer06Active: false,
      isLayer07Active: false,

      layerLegendHTMLcontent: null,

      layerZone01: {
        name: "Зона затоплення I",
        layers: [28],
        url: L.esri.dynamicMapLayer({
          url: mapServerUrl,
          layers: [28],
          zIndex: 50
        })
      },
      layerZone02: {
        name: "Зона затоплення II",
        layers: [29],
        url: L.esri.dynamicMapLayer({
          url: mapServerUrl,
          layers: [29],
          zIndex: 50
        })
      },

      layer01: {
        name: "ЗЕМЛЕКОРИСТУВАННЯ",
        layers: [0],
        url: L.esri.dynamicMapLayer({
          url: mapServerUrl,
          layers: [0],
          zIndex: 100
        })
      },
      layer02: {
        name: "НАСЕЛЕННЯ І ПОСЕЛЕННЯ",
        layers: [2, 3, 4, 5],
        url: L.esri.dynamicMapLayer({
          url: mapServerUrl,
          layers: [1],
          zIndex: 100
        })
      },
      layer03: {
        name: "ТУРИЗМ І ВІДПОЧИНОК",
        layers: [16, 17, 18],
        url: L.esri.dynamicMapLayer({
          url: mapServerUrl,
          layers: [15],
          zIndex: 100
        })
      },
      layer04: {
        name: "ІНФРАСТРУКТУРА",
        layers: [7, 8, 9, 10],
        url: L.esri.dynamicMapLayer({
          url: mapServerUrl,
          layers: [6],
          zIndex: 100
        })
      },
      // layer05: {
      //   name: "ТРАНСПОРТ",
      //   layers: [],
      //   url: L.esri.dynamicMapLayer({
      //     url:
      //       mapServerUrl,
      //     layers: [],
      //     zIndex: 100
      //   })
      // },
      layer06: {
        name: "ГОСПОДАРСТВО",
        layers: [12, 13, 14],
        url: L.esri.dynamicMapLayer({
          url: mapServerUrl,
          layers: [11],
          zIndex: 100
        })
      },
      layer07: {
        name: "ЛАНДШАФТ і ЕКОЛОГІЯ",
        layers: [21, 22, 24, 25, 26, 27],
        url: L.esri.dynamicMapLayer({
          url: mapServerUrl,
          layers: [19],
          zIndex: 100
        })
      },

      legendUrl: mapServerUrl + "/legend?f=json",
      legendLayers: []
    };
  },

  methods: {
    createLegendForLayers: function(layer_ids) {
      let data = this.legendLayers;
      var html = '<ul class="legend">';

      layer_ids.forEach(function(id) {
        data.forEach(function(item) {
          let layerName = item.layerName;
          let layerId = item.layerId;

          if (id === layerId) {
            html +=
              '<li class="legend-title"><span>' + layerName + "</span><ul>";

            console.log(layerName, item.legend.length);
            item.legend.forEach(function(legend_item) {
              let label = legend_item.label
                .replace(",000000", "")
                .replace(",000000", "")
                .replace(",000001", "");
              html +=
                '<li><img class="icon" src="data:' +
                legend_item.contentType +
                ";base64," +
                legend_item.imageData +
                '"><span>' +
                label +
                "</span></li>";
            });
            html += "</ul></li>";
          }
        });
      });

      html += "</ul>";

      this.layerLegendHTMLcontent = html;
    },

    offLayersControl: function() {
      this.isLayer01Active = false;
      this.isLayer02Active = false;
      this.isLayer03Active = false;
      this.isLayer04Active = false;
      // this.isLayer05Active = false;
      this.isLayer06Active = false;
      this.isLayer07Active = false;

      document.getElementById("map").style.cursor = "";
    },
    offLayers: function() {
      this.map.removeLayer(this.layer01.url);
      this.map.removeLayer(this.layer02.url);
      this.map.removeLayer(this.layer03.url);
      this.map.removeLayer(this.layer04.url);
      // this.map.removeLayer(this.layer05.url);
      this.map.removeLayer(this.layer06.url);
      this.map.removeLayer(this.layer07.url);

      document.getElementById("map").style.cursor = "";
    },

    layerZone1Click: function() {
      this.isZone1Active = !this.isZone1Active;
      if (this.isZone2Active) {
        this.isZone2Active = !this.isZone2Active;
      }

      //this.isZone2Active = false;

      if (this.isZone1Active) {
        this.map.addLayer(this.layerZone01.url);
        this.map.removeLayer(this.layerZone02.url);
        console.log("add zone 1, remove zone 2");
      } else {
        console.log("remove zone 1");
        this.map.removeLayer(this.layerZone01.url);
        this.offLayersControl();
      }

      this.zoneDescription =
        "Зона постійного підтоплення яка виникне у разі підвищення на 0,8 м загального рівня моря у 2100 р. за сценарієм збереження рівня викидів парникових газів РТК8.5";
    },
    layerZone2Click: function() {
      this.isZone2Active = !this.isZone2Active;
      if (this.isZone1Active) {
        this.isZone1Active = !this.isZone1Active;
      }

      //this.isZone1Active = false;

      if (this.isZone2Active) {
        this.map.addLayer(this.layerZone02.url);
        this.map.removeLayer(this.layerZone01.url);
        console.log("add zone 2, remove zone 1");
      } else {
        console.log("remove zone 2");
        this.map.removeLayer(this.layerZone02.url);
        this.offLayersControl();
      }

      this.zoneDescription =
        "Розширена зона підтоплення внаслідок проходження циклонів і штормів, за рахунок чого рівень моря тимчасово підвищується на різних ділянках додатково на 0,2-0,9 м";
    },

    layer01Click: function() {
      if (!this.isZone1Active && !this.isZone2Active) {
        this.layerZone1Click();
      }

      this.isLayer01Active = !this.isLayer01Active;

      this.isLayer02Active = false;
      this.isLayer03Active = false;
      this.isLayer04Active = false;
      // this.isLayer05Active = false;
      this.isLayer06Active = false;
      this.isLayer07Active = false;

      this.offLayers();

      if (this.isLayer01Active) {
        this.map.addLayer(this.layer01.url);
        document.getElementById("map").style.cursor = "crosshair";
      }

      this.createLegendForLayers(this.layer01.layers);
    },
    layer02Click: function() {
      if (!this.isZone1Active && !this.isZone2Active) {
        this.layerZone1Click();
      }

      this.isLayer02Active = !this.isLayer02Active;

      this.isLayer01Active = false;
      this.isLayer03Active = false;
      this.isLayer04Active = false;
      // this.isLayer05Active = false;
      this.isLayer06Active = false;
      this.isLayer07Active = false;

      this.offLayers();

      if (this.isLayer02Active) {
        this.map.addLayer(this.layer02.url);
        document.getElementById("map").style.cursor = "crosshair";
      }

      this.createLegendForLayers(this.layer02.layers);
    },
    layer03Click: function() {
      if (!this.isZone1Active && !this.isZone2Active) {
        this.layerZone1Click();
      }

      this.isLayer03Active = !this.isLayer03Active;

      this.isLayer01Active = false;
      this.isLayer02Active = false;
      this.isLayer04Active = false;
      this.isLayer05Active = false;
      this.isLayer06Active = false;
      this.isLayer07Active = false;

      this.offLayers();

      if (this.isLayer03Active) {
        this.map.addLayer(this.layer03.url);
        document.getElementById("map").style.cursor = "crosshair";
      }

      this.createLegendForLayers(this.layer03.layers);
    },
    layer04Click: function() {
      if (!this.isZone1Active && !this.isZone2Active) {
        this.layerZone1Click();
      }

      this.isLayer04Active = !this.isLayer04Active;

      this.isLayer01Active = false;
      this.isLayer02Active = false;
      // this.isLayer03Active = false;
      this.isLayer05Active = false;
      this.isLayer06Active = false;
      this.isLayer07Active = false;

      this.offLayers();

      if (this.isLayer04Active) {
        this.map.addLayer(this.layer04.url);
        document.getElementById("map").style.cursor = "crosshair";
      }

      this.createLegendForLayers(this.layer04.layers);
    },
    // layer05Click: function() {
    //   if (!this.isZone1Active && !this.isZone2Active) {
    //     this.layerZone1Click();
    //   }

    //   this.isLayer05Active = !this.isLayer05Active;

    //   this.isLayer01Active = false;
    //   this.isLayer02Active = false;
    //   // this.isLayer03Active = false;
    //   this.isLayer04Active = false;
    //   this.isLayer06Active = false;
    //   this.isLayer07Active = false;

    //   this.offLayers();

    //   if (this.isLayer05Active) {
    //     this.map.addLayer(this.layer05.url);
    //     document.getElementById("map").style.cursor = "crosshair";
    //   }

    //   this.createLegendForLayers(this.layer05.layers);
    // },
    layer06Click: function() {
      if (!this.isZone1Active && !this.isZone2Active) {
        this.layerZone1Click();
      }

      this.isLayer06Active = !this.isLayer06Active;

      this.isLayer01Active = false;
      this.isLayer02Active = false;
      this.isLayer03Active = false;
      this.isLayer04Active = false;
      // this.isLayer05Active = false;
      this.isLayer07Active = false;

      this.offLayers();

      if (this.isLayer06Active) {
        this.map.addLayer(this.layer06.url);
        document.getElementById("map").style.cursor = "crosshair";
      }

      this.createLegendForLayers(this.layer06.layers);
    },
    layer07Click: function() {
      if (!this.isZone1Active && !this.isZone2Active) {
        this.layerZone1Click();
      }

      this.isLayer07Active = !this.isLayer07Active;

      this.isLayer01Active = false;
      this.isLayer02Active = false;
      this.isLayer03Active = false;
      this.isLayer04Active = false;
      // this.isLayer05Active = false;
      this.isLayer06Active = false;

      this.offLayers();

      if (this.isLayer07Active) {
        this.map.addLayer(this.layer07.url);
        document.getElementById("map").style.cursor = "crosshair";
      }

      this.createLegendForLayers(this.layer07.layers);
    },
    showInfo: function(error, featureCollection) {
      if (error || featureCollection.features.length === 0) {
        this.selectedFeature =
          "Наблизьтесь до потрібної ділянки карти та клацніть для отримання додаткових відомостей про об’єкт";
        return false;
      } else {
        let properties = featureCollection.features[0].properties;
        let content = [];
        for (let prop in properties) {
          if (
            properties[prop] &&
            properties[prop] != "Null" &&
            prop != "Shape"
            // prop != "OBJECTID" &&
            // prop != "FID"
          ) {
            let prop_value = properties[prop];
            prop = prop === "Shape_Length" ? "Length" : prop;
            prop = prop === "Shape_Area" ? "Area" : prop;

            content.push("<b>" + prop + "</b>: " + prop_value + "<br>");
          }
        }
        this.selectedFeature = content.join("");
      }
    }
  },

  mounted() {
    // get legend data
    axios
      .get(this.legendUrl)
      .then(response => (this.legendLayers = response.data.layers));

    const map = L.map("map", {
      zoomControl: false
    }).setView([46.37725, 33.25561], 8);

    map.invalidateSize();

    let zoomControl = L.control
      .zoom({
        position: "bottomright"
      })
      .addTo(map);

    this.map = map;

    let layerOSM = L.tileLayer(
      "http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      {
        attribution:
          '© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
      }
    );

    let layerMapboxImagery = new L.tileLayer(
      "http://{s}.tiles.mapbox.com/v4/openstreetmap.map-inh7ifmo/{z}/{x}/{y}.png?access_token=pk.eyJ1IjoiaGFzdCIsImEiOiJjamttNzNsbzUyMmZ5M3ZtejMzaHdtMjZ6In0.AM5GdLMffJOY9hRT4sYY4g",
      {
        attribution: '© <a href="https://www.mapbox.com/about/maps/">Mapbox</a>'
      }
    );

    let layerMapboxLight = new L.tileLayer(
      "http://{s}.tiles.mapbox.com/v4/mapbox.light/{z}/{x}/{y}.png?access_token=pk.eyJ1IjoiaGFzdCIsImEiOiJjamttNzNsbzUyMmZ5M3ZtejMzaHdtMjZ6In0.AM5GdLMffJOY9hRT4sYY4g",
      {
        attribution:
          '© <a href="https://www.mapbox.com/about/maps/">Mapbox</a> © <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a> <strong><a href="https://www.mapbox.com/map-feedback/" target="_blank">Improve this map</a></strong>'
      }
    );

    let layerEsriWorldImagery = new L.tileLayer(
      "http://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}",
      {
        attribution:
          '© <a href="http://www.esri.com/">Esri</a>, i-cubed, USDA, USGS, AEX, GeoEye, Getmapping, Aerogrid, IGN, IGP, UPR-EGP, and the GIS User Community',
        maxZoom: 20,
        maxNativeZoom: 18
      }
    );

    let providers = {};

    providers["OpenStreetMap_BlackAndWhite"] = {
      title: "OSM B&W",
      icon:
        "data:image/png;base64,",
      layer: L.tileLayer(
        "http://{s}.tiles.wmflabs.org/bw-mapnik/{z}/{x}/{y}.png",
        {
          maxZoom: 20,
          maxNativeZoom: 16,
          attribution:
            '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>'
        }
      )
    };

    providers["OpenStreetMap_Mapnik"] = {
      title: "OSM Mapnik",
      icon:
        "data:image/png;base64,",
      layer: L.tileLayer("http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        maxZoom: 20,
        maxNativeZoom: 19,
        attribution:
          '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>'
      })
    };

    providers["Esri_WorldTerrain"] = {
      title: "ESRI Terrain",
      icon:
        "data:image/png;base64,",
      layer: L.tileLayer(
        "http://server.arcgisonline.com/ArcGIS/rest/services/World_Terrain_Base/MapServer/tile/{z}/{y}/{x}",
        {
          attribution:
            "Tiles &copy; Esri &mdash; Source: USGS, Esri, TANA, DeLorme, and NPS",
          maxZoom: 20,
          maxNativeZoom: 10
        }
      )
    };

    providers["Esri_OceanBasemap"] = {
      title: "ESRI Ocean",
      icon:
        "data:image/png;base64,",
      layer: L.tileLayer(
        "http://server.arcgisonline.com/ArcGIS/rest/services/Ocean_Basemap/MapServer/tile/{z}/{y}/{x}",
        {
          attribution:
            "Tiles &copy; Esri &mdash; Sources: GEBCO, NOAA, CHS, OSU, UNH, CSUMB, National Geographic, DeLorme, NAVTEQ, and Esri",
          maxZoom: 20,
          maxNativeZoom: 10
        }
      )
    };

    providers["CartoDB_Positron"] = {
      title: "CartoDB",
      icon:
        "data:image/png;base64,",
      layer: L.tileLayer(
        "http://{s}.basemaps.cartocdn.com/light_all/{z}/{x}/{y}.png",
        {
          attribution:
            '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a> &copy; <a href="http://cartodb.com/attributions">CartoDB</a>',
          subdomains: "abcd",
          maxZoom: 20,
          maxNativeZoom: 19
        }
      )
    };

    providers["ESRI_Imagery"] = {
      title: "ESRI Imagery",
      icon:
        "data:image/png;base64,",
      layer: L.tileLayer(
        "http://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}",
        {
          maxZoom: 20,
          maxNativeZoom: 18,
          attribution:
            '© <a href="http://www.esri.com/">Esri</a>, i-cubed, USDA, USGS, AEX, GeoEye, Getmapping, Aerogrid, IGN, IGP, UPR-EGP, and the GIS User Community'
        }
      )
    };

    let layers = [];
    for (let providerId in providers) {
      layers.push(providers[providerId]);
    }

    let ctrl = L.control
      .iconLayers(layers, {
        maxLayersInRow: 6
      })
      .addTo(map);

    this.layer01.url.bindPopup((error, featureCollection) => {
      this.showInfo(error, featureCollection);
    });
    this.layer02.url.bindPopup((error, featureCollection) => {
      this.showInfo(error, featureCollection);
    });
    this.layer03.url.bindPopup((error, featureCollection) => {
      this.showInfo(error, featureCollection);
    });
    this.layer04.url.bindPopup((error, featureCollection) => {
      this.showInfo(error, featureCollection);
    });
    // this.layer05.url.bindPopup((error, featureCollection) => {
    //   this.showInfo(error, featureCollection);
    // });
    this.layer06.url.bindPopup((error, featureCollection) => {
      this.showInfo(error, featureCollection);
    });
    this.layer07.url.bindPopup((error, featureCollection) => {
      this.showInfo(error, featureCollection);
    });

    // 3 Applewood Drive, Hopewell Township, NJ, United States
    var circle = {};
    var googleSearchControl = new L.Control.GPlaceAutocomplete({
      position: "topright",
      autocomplete_options: {
        componentRestrictions: {
          country: "ua"
        }
      },
      callback: function(location) {
        if ("geometry" in location) {
          var lat = location.geometry.location.lat();
          var lng = location.geometry.location.lng();

          map.panTo([lat, lng]);
          map.fitBounds([
            [
              location.geometry.viewport.getSouthWest().lat(),
              location.geometry.viewport.getSouthWest().lng()
            ],
            [
              location.geometry.viewport.getNorthEast().lat(),
              location.geometry.viewport.getNorthEast().lng()
            ]
          ]);

          map.removeLayer(circle);
          circle = L.circle([lat, lng], {
            color: "#FFFF00",
            weight: 2,
            fillColor: "#0000FF",
            fillOpacity: 0.9,
            radius: 50
          }).addTo(map);
        }
      }
    });
    map.addControl(googleSearchControl);
    document.getElementsByClassName("leaflet-gac-control")[0].placeholder =
      "Пошук за назвами та адресою";

    map.on("click", function() {
      map.removeLayer(circle);
    });

    // map.flyTo([50,36], 18, {
    //   animate: true
    // })
  }
};
</script>

<style lang="scss">
@import "../../node_modules/leaflet/dist/leaflet.css";
@import "../leaflet-google-places-autocomplete/leaflet-gplaces-autocomplete.css";
@import "../iconlayers-0.2.0/iconLayers.css";

#map {
  width: 100%;
  height: 100%;
}

.leaflet-popup-content-wrapper {
  border-radius: 3px;
}

.leaflet-popup-content {
  margin: 5px 5px 5px 5px;
}

.nav-container a,
.nav-container a:hover,
.nav-container a:active,
.nav-container a:visited {
  color: white;
  text-decoration: none;
}

.nav-container {
  position: absolute;
  left: 10px;
  top: 10px;
  color: white;
  opacity: 0.9;
  text-transform: uppercase;
  z-index: 450;
}
.nav-container .nav-help {
  color: black;
  padding: 0 6px;
  letter-spacing: 0.08em;
  width: 503px;
}
.nav-container .nav-help.scenario {
  background-color: rgba(0, 174, 239, 0.8);
}
.nav-container .nav-help.theme {
  background-color: rgba(255, 232, 0, 0.8);
}
.nav-container ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.nav-container ul:not(.legend) {
  -webkit-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
.nav-container ul:not(.legend) li {
  padding: 0.5em;
  margin: 0.5em 0;
  line-height: 1em;
  letter-spacing: 0.08em;
  white-space: pre-line;
  cursor: pointer;
  height: 2em;
}
.nav-container ul:not(.legend) li a {
  font-weight: 700;
  font-size: 1.2em;
}
.nav-container ul:not(.legend) li:hover,
.nav-container ul:not(.legend) li.selected {
  color: black;
}
.nav-container ul:not(.legend) li:hover a,
.nav-container ul:not(.legend) li.selected a {
  color: black;
}
.nav-container nav {
  width: 450px;
}
.nav-container nav div:first-child {
  display: flex;
  display: -webkit-flex;
  display: -ms-flexbox;
}
.nav-container nav.narrow {
  width: 515px;
}
.nav-container nav ul:first-child {
  margin-right: 6px;
}
.nav-container nav:not(:last-child) {
  margin-bottom: 0.2em;
}
.nav-container nav.scenario li {
  background-color: rgba(0, 0, 0, 0.8);
}
.nav-container nav.scenario li .subtitle {
  font-family: "Open Sans Condensed", sans-serif;
  font-size: 0.8em;
}
.nav-container nav.scenario li:hover,
.nav-container nav.scenario li.selected {
  background-color: rgba(0, 174, 239, 0.8);
}
.nav-container nav.scenario .infobox {
  background-color: rgba(0, 0, 0, 0.8);
  line-height: 1.5em;
}
.nav-container nav.theme li {
  background-color: rgba(0, 0, 0, 0.6);
  font-size: 0.9em;
}
.nav-container nav.theme li:hover,
.nav-container nav.theme li.selected {
  background-color: rgba(255, 232, 0, 0.8);
}
.nav-container nav.theme .infobox {
  background-color: rgba(0, 0, 0, 0.6);
  width: 230px;
  max-height: 500px;
  overflow-y: auto;
}
.nav-container nav .infobox {
  padding: 6px 10px;
  text-transform: none;
  font-size: 0.8em;
  display: none;
}
.nav-container nav .infobox li {
  background-color: transparent;
  padding-bottom: 0.3em;
}
.nav-container nav .infobox li img.icon {
  margin-right: 8px;
  vertical-align: top;
  width: 20px;
  height: 20px;
}
.nav-container nav .infobox li:hover {
  color: white;
  background-color: transparent;
}

.nav-container nav .infobox li.legend-title span:first-child {
  text-decoration: underline;
  padding-bottom: 0.3em;
}

.legend .legend-title ul li {
  margin: 2px 0;
  padding: 0;
  line-height: 2.2em;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.layer-info {
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  position: absolute;
  top: 50px;
  right: 11px;
  z-index: 1000;
  padding: 10px;
  max-width: 293px;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.leaflet-bar a,
.leaflet-bar a:hover,
.leaflet-bar a.leaflet-disabled {
  background-color: rgba(0, 0, 0, 0.8);
  color: rgba(204, 204, 204, 0.8);
}
.leaflet-bar a:hover {
  color: white;
}
.leaflet-bar a.leaflet-disabled {
  color: rgba(77, 77, 77, 0.8);
}

.leaflet-bar,
.leaflet-bar a:first-child,
.leaflet-bar a:last-child {
  border-radius: 0px;
}

.leaflet-control-attribution.leaflet-control {
  background: none;
  text-align: right;
  line-height: 12px;
}
.leaflet-control-attribution.leaflet-control a {
  color: black;
}

.leaflet-iconLayers-layer {
  border-radius: 0px;
  box-shadow: 0 0 0px #000;
  border-style: solid;
  border-width: 1px;
}

.leaflet-iconLayers-layerTitle {
  color: white;
  background-color: rgba(0, 0, 0, 0.6);
}

.leaflet-container {
  font-family: "Open Sans", sans-serif;
}
.leaflet-gac-control {
  font-family: "Open Sans", sans-serif;
}
</style>



